html {
  font-size: 62.5%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.4rem;
  background-color: #F5F5F7;
  color: #262626;
  overflow-x: hidden;
}

.big-container {
  max-width: 1718px;
  margin: 0 auto;
  padding: 0px 15px;
}

.container {
  max-width: 301px;
  margin: 0 auto;
  padding: 0px 14px;
}

h1, p, h2, h3, h4, h5 {
  margin: 0px;
}

h1, h2, h3, h4, h5, h6 {
  color: #262626;
  font-family: inherit;
  font-style: normal;
  font-weight: 800;
  line-height: 135%;
  /* 54px */
  margin: 0px 0px 20px 0px;
}

h1, .h1 {
  color: #FFF;
  font-family: inherit;
  font-size: 3.2rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: uppercase;
  word-break: break-word;
}

h2, .h2 {
  font-size: 3rem;
}

h3, .h3 {
  font-size: 2rem;
}

.upper {
  text-transform: uppercase;
}

.thin {
  font-weight: 600;
}

.centred {
  text-align: center;
}

h4, .h4 {
  font-size: 2rem;
  line-height: 195.9%;
}

ul {
  padding-left: 0px;
  list-style: none;
}

a {
  text-decoration: none;
  transition: all ease 0.3s;
}

.header-up {
  color: #BDBDBD;
  font-family: inherit;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 700;
  line-height: 135%;
  /* 24.3px */
  letter-spacing: 1.26px;
  text-transform: uppercase;
  margin-bottom: 12px;
}

.bold-gray-text {
  color: #6A6A6A;
  text-align: center;
  font-family: inherit;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 700;
  line-height: 180.9%;
  /* 25.326px */
}

.small-header {
  color: #262626;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 800;
  line-height: 142.5%;
  /* 19.95px */
  margin-bottom: 14px;
}

.gray-text {
  color: #6A6A6A;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 400;
  line-height: 142.5%;
  /* 19.95px */
}

.norm-gray-text {
  color: #676573;
  font-family: inherit;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 600;
  line-height: 195.9%;
  /* 27.426px */
}

.big-gray-text {
  color: #676573;
  font-family: inherit;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 600;
  line-height: 195.9%;
  /* 27.426px */
}

.thin {
  font-weight: 500;
}

.subheader-text {
  color: #444;
  font-family: inherit;
  font-size: 2rem;
  font-style: normal;
  font-weight: 700;
  line-height: 150.9%;
  /* 30.18px */
  margin-bottom: 21px;
}

.subheader-normal {
  color: #444;
  font-family: inherit;
  font-size: 2rem;
  font-style: normal;
  font-weight: 600;
  line-height: 195.9%;
  /* 39.18px */
}

.standart-list {
  padding-left: 30px;
  margin-bottom: 30px;
  color: #676573;
}
.standart-list li {
  color: #676573;
  margin-bottom: 20px;
  position: relative;
}
.standart-list li:before {
  content: "";
  position: absolute;
  left: -25px;
  top: 8px;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background-color: #c4c4c4c4;
}

.button {
  text-align: center;
  font-family: inherit;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 600;
  line-height: 135%;
  /* 21.6px */
  padding: 15px 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: 1px solid #3A55C1;
  cursor: pointer;
  transition: all ease-in-out 0.3s;
  box-sizing: border-box;
}
.button-empty {
  border: 1px solid #5475F8;
  color: #5475F8;
}
.button-empty:hover {
  border: 1px solid #095CD9;
  background: #095CD9;
  color: #fff;
}
.button-blue {
  border: 1px solid #095CD9;
  background: #095CD9;
  color: #fff;
}
.button-blue:hover {
  border: 1px solid #5475F8;
  color: #5475F8;
  background: transparent;
}

ul.breadcrumb {
  list-style: none;
  color: #fff;
  display: flex;
  flex-flow: wrap;
  margin-left: 0px;
}

ul.breadcrumb a,
ul.breadcrumb span {
  display: inline;
  margin-right: 10px;
  color: #fff;
  font-family: inherit;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  margin-bottom: 10px;
}

ul.breadcrumb a:after {
  content: "";
  display: inline-block;
  width: 10px;
  height: 10px;
  background-image: url("../img/icons/bread-arr.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  margin-left: 11px;
}

ul.breadcrumb a {
  text-decoration: none;
}

ul.breadcrumb a:hover {
  text-decoration: underline;
}

.two-cols {
  margin-bottom: 60px;
}

.only-desctop {
  display: none;
}

.slider-item {
  width: 200px;
  height: 300px;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
}
.slider-item .image-wrap {
  overflow: hidden;
}
.slider-item .image-wrap img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  transition: all 0.3s ease;
}
.slider-item:hover .image-wrap img {
  transform: scale(1.1);
}

.slider-texts {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  height: -webkit-fill-available;
  width: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  color: #fff;
  padding: 30px 0px;
  text-align: center;
}
.slider-header {
  font-family: inherit;
  font-size: 2rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  max-width: 152px;
  margin: 0 auto;
}
.slider-bottom-text {
  font-family: inherit;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  max-width: 162px;
  margin: 0 auto;
}

.slick-wrap {
  margin-bottom: 100px;
}

.slider {
  width: 130%;
}
.slider-item {
  margin-right: 15px;
}

.slider-btns {
  width: 40px;
  height: 40px;
  border: 1px solid #111;
  padding: 15px 17px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  transition: all ease 0.3s;
}
.slider-btns:hover {
  background-color: #fff;
  filter: invert(100%);
}
.slider-btns:hover svg path {
  fill: #fff;
}

.prev-btn {
  background-image: url("../img/icons/slick-left.svg");
}

.next-btn {
  background-image: url("../img/icons/slick-right.svg");
}

.btn-wrap.main-type .prev-btn {
  margin-right: 36px;
}

@media screen and (min-width: 500px) {
  .container {
    max-width: 90%;
    padding: 0px 15px;
  }
}
@media screen and (min-width: 760px) {
  .container {
    max-width: 700px;
  }
}
@media screen and (min-width: 960px) {
  .container {
    max-width: 900px;
  }

  .two-cols {
    margin-bottom: 85px;
    display: flex;
    justify-content: space-between;
  }
  .two-cols .col {
    width: 48%;
  }
  .two-cols .col:nth-child(2) .pros-list {
    padding-left: 28px;
  }
  .two-cols .col .pros-list {
    padding-left: 28px;
  }

  .container-no-end {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .no-end-wrap {
    max-width: 370px;
    margin-right: 97px;
    margin-bottom: 219px;
  }

  .only-mobile {
    display: none;
  }

  .only-desctop {
    display: block;
  }

  .subheader-normal {
    font-size: 2.4rem;
    margin-bottom: 24px;
  }
}
@media screen and (min-width: 1120px) {
  .container {
    max-width: 1000px;
  }
}
@media screen and (min-width: 1200px) {
  .big-container {
    max-width: 1318px;
  }

  h1, .h1 {
    font-size: 7rem;
    font-weight: 800;
    line-height: 116.5%;
    /* 81.55px */
    text-transform: uppercase;
  }

  h2, .h2 {
    font-size: 4rem;
  }

  h3, .h3 {
    font-size: 5rem;
  }

  h4, .h4 {
    font-size: 2.8rem;
  }

  .norm-gray-text {
    font-size: 1.6rem;
  }

  .big-gray-text {
    font-size: 2rem;
    line-height: 195.9%;
    margin-bottom: 120px;
  }

  .header-up {
    font-size: 1.8rem;
    letter-spacing: 1.62px;
    margin-bottom: 15px;
  }

  .bold-gray-text {
    font-size: 1.8rem;
    line-height: 145%;
  }

  .no-end-wrap {
    padding-left: 86px;
    padding-top: 11px;
  }
  .no-end-wrap .h4 {
    margin-bottom: 25px;
  }

  .norm-gray-text {
    line-height: 219.9%;
  }

  .no-end-wrap .norm-gray-text {
    margin-bottom: 44px;
  }

  .slider-btns {
    width: 60px;
    height: 60px;
  }

  .btn-wrap.main-type .prev-btn {
    margin-right: 47px;
  }

  .slider-item {
    width: 280px;
    height: 400px;
  }

  .slider-texts {
    padding: 50px 0px;
  }
  .slider-header {
    max-width: 250px;
    font-size: 2.4rem;
  }
  .slider-bottom-text {
