@use "../foundation/base" as b;

.cards-box{
  display: flex;
  flex-flow: wrap;
  margin-bottom: 100px;
}

.card{
    display: block;
    width: 130px;
    height: 190px;
    border-radius: 20px;
    position: relative;
    overflow: hidden;
    margin-right: 14px;
    margin-bottom: 40px;;
    .image-wrap {
      overflow: hidden;
      img{
        position: absolute;
        top: 0px;      
        left: 0px;
        transition: all 0.3s ease;  
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &:hover{
      img{
        transform: scale(1.1)
      }
    }
  }
  


  .describe-block{
    margin: 0 auto;
    max-width: 301px;
    padding: 30px 14px;
  }

.card-header{
    color: #FFF;
    text-align: center;
    font-family: inherit;
    font-size: 1.4rem;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.full-width{
}

.white{
    background-color: #fff;

}
.describe-block{
    padding: 40px 10px;
    display: flex;
    flex-direction: column;
    margin: 60px auto;;
}
.describe-item{
    margin-bottom: 40px;;
}

.describe-header {
    display: flex;;
    justify-content: flex-start;
}
.describe-head{
    color: #000;
    font-family: inherit;
    font-size: 2rem;
    font-style: normal;
    font-weight: 700;
    line-height: 140.4%; /* 28.08px */
    margin-bottom: 20px;
}

.describe-image-wrap{
    width: 30px;
    height: 30px;
    margin-right: 24px;
    img{
        width: auto;
        height: 100%;
        object-fit: cover;
    }
}

.describe-text{
    color: #676573;
    font-family: inherit;
    font-size: 1.4rem;
    font-style: normal;
    font-weight: 600;
    line-height: 195.9%; /* 27.426px */ 
}

.card-bottom{
    color: #FFF;
    text-align: center;
    font-family: inherit;
    font-size: 1.0rem;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.information-part{
    // overflow: hidden;
}

.wrap-illustration{
    width: 100%;
    position: relative;
    height: 350px;

    img{
        width: 562px;
        position: absolute;
        right: -250px;
    }
}

.about{
    &-list{
        margin-bottom: 100px;
    }
    &-item{
        display: flex;
        margin-bottom: 40px;
    }
    &-text{
        color: #000;
        font-family: inherit;
        font-size: 1.4rem;
        font-style: normal;
        font-weight: 500;
        line-height: 195.9%; /* 27.426px */
    }

}
.wrap-about-img{
    width: 30px;
    min-width: 30px;;
    height: 30px;
    margin-right: 24px;
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.bold-text{
    color: #111;
    font-family: inherit;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 700;
    line-height: 187.4%; /* 29.984px */
}

.ordered-block{
    display: flex;
    flex-direction: column;
    .h2{
        order: 1;
    }
    .wrap-illustration{
        order: 2;
    }
    .big-gray-text{
        order: 3;
    }
}

.table {
    &-list{
        padding-top: 30px;
        padding-bottom: 30px;
        .norm-gray-text{
            width: 80% ;  
            text-align: left;              
        }
        .percentage {
            max-width: 40px;
        }
    }
    &-item{
        display: flex;
        align-items: center;
        padding: 27px 0px;        
        justify-content: space-between;
        border-bottom: 1px solid #C9C9C9;
        &-image-wrap{
            width: 24px;
            height: 24px;
            margin-right: 24px;

            img{
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
        p {
            font-weight: 400;
        }

    }
}
.slider-big-wrap{
    width: 300px;
    margin: 0 auto 100px;
}

.slider-big{
    margin-bottom: 20px;;
    .wrap-item-image{
        max-width: 296px;
        width: 296px;
        height: 250px;
        
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    
    }
}

.slider-prevs{
    .wrap-prevs-item, .item, .slick-slide{
        max-width: 92px!important;
        width: 92px!important;
        height: 90px!important;
        margin-right: 8px;
        margin-bottom: 8px;;
        display: block;
        cursor: pointer;
        img{
            width: 100%;
            height: 100%;
        //             max-width: 92px!important;
        // width: 92px!important;
        // height: 90px!important;
            object-fit: cover;
        }
    }
    .item.slick-slide.slick-current.slick-active{
        border-bottom: 2px solid b.$dark-accent;
        padding-bottom: 10px;
    }
}

.slick-wrap-w{
    width: 300px;
    position: relative;
    padding: 20px 0px 50px;
    margin: 0 auto;
    .second-type{
        position: absolute;
        top:50%;
        width: 100%;
        display: flex;
        justify-content: space-between;
        transform: translateY(-50%)
    }
    .slider-w{
        width: 138px;
        margin: 0 auto;
        .slider-item{
            width: 138px;
            height: 200px;
            margin: 0px;;

        }
        .slider-header{
            font-size: 1.4rem;;
        }
        .slider-bottom-text{
            font-size: 1.0rem;;
        }
    }
}

.slider-w {
    // .slider-item{
    //     width: 300px;;
    // }

}



.map-container{
    margin-bottom: 100px;;
    .norm-gray-text{
        margin-bottom: 30px;
    }
}

.map-palce{
    width: 295px;
    height: 295px;
    margin-bottom: 100px;
    iframe{
        width: 100%;
        height: 100%;
    }
}


@media screen and (min-width: 500px){
    .cards-box{
        justify-content: center;
    }
    .card{
        margin: 20px 10px;
    }
    .describe-block{
        max-width: 90%;
        padding: 30px 15px;
    }
    .wrap-illustration img {
        right: -150px;
    }
    .about-list{
        max-width: 980px;
    }
    .slider-big-wrap{
        width: 300px;
        margin: 0 auto 100px;
    }
    .map-palce{
        width: 100%;
        min-height: 295px;
    }

}


@media screen and (min-width: 970px){
    .describe-block{
        flex-direction: row;
        flex-wrap: wrap;
        max-width: 960px;
        padding: 60px 35px;

    }
    .describe-item{
        width: 48%;
    }
    .ordered-block{
        position: relative;
        .h2{
            order: 1;
            margin-bottom: 40px;;
        }
        .wrap-illustration{
            order: 3;
            position: absolute;
            left: 0px;
            right: auto;
            top: 0px;
            img{
                right: 0px;
            }
        }
        .big-gray-text{
            margin-bottom: 130px;;
            order: 2;
            max-width: 590px;;
        }
    }
    .about-text{
        font-size: 2.0rem;
        font-weight: 500;
        line-height: 195.9%; /* 39.18px */
    }
    .about-item{
        margin-bottom: 50px;
    }
    .wrap-about-img{
        width: 40px;
        height: 40px;
        min-width: 40px;
        max-width: 40px;
        margin-right: 40px;;
    }
    .bold-text{
        font-size: 2.4rem;
        font-weight: 700;
    }
    .slider-big-wrap{
        width: 600px;
        margin: 0 auto 100px;
    }
    .slider-big .wrap-item-image {
        height: 590px;
        max-width: 600px;
        width: 600px;
    }
    .slick-wrap-w{
        width: 800px;
        margin-bottom: 150px;;
        .slider-w{
            width: 600px;
        }
    }
    .slick-wrap-w .slider-w .slider-item{
        width: 180px;
        height: 300px;
        margin: 0px 15px;
    }
    .slick-wrap-w.slider-w:before{
        display: none;

    }

}

@media screen and (min-width: 1120px){
    .describe-block{
        max-width: 1100px;
    }
}

@media screen and (min-width: 1200px){
    .card-box{
        margin-bottom: 150px;
    }
    .card{
        width: 280px;
        height: 400px;
        margin-right: 60px;
        margin-bottom: 40px;;
    }
    .card-header{
        font-size: 3.0rem;
    }
    .card-bottom{
        font-size: 1.6rem;
    }
    .white{
        background-color: transparent;
    }
    .describe-block{
        background-color: #fff;
        // padding: 30px 15px
    }
    .cards-box{
        justify-content: flex-start;
    }
    .resize-padding{
        padding-right: 160px;
    }
    .table-item-image-wrap {
        height: 30px;
        margin-right: 30px;
        width: 30px;

    }
    .slider-big-wrap{
        width: 100%;
        margin: 0 auto 100px;
    }
    .slider-big .wrap-item-image {
        min-height: 590px;
        max-width: 100%;
        width: 100%;
    }
    .slider-big{
        margin-bottom: 40px;;
    }
    .slider-prevs{
        .slick-track{
            margin-left: 0px!important;
        }
        .wrap-prevs-item, .slick-slide{
            max-width: 101px;
            width: 101px;
            height: 101px;
            margin-right: 15px;
            display: block;
        }
    }
    .slick-wrap-w{
        width: 1000px;

        .slider-w{
            width: 800px;
        }
    }
    .slick-wrap-w .slider-w .slider-item{
        width: 180px;
        height: 300px;
        margin: 0px 12px;
    }
    .map-palce{
        width: 100%;
        height: 575.556px;
        margin-bottom: 150px;;
    }
    .map-container .norm-gray-text.bold{
        max-width: 50%;
        margin-bottom: 54px;
    }
    .map-container .norm-gray-text.oncenter{
        max-width: 80%;
        margin: 0 auto;
        text-align: center;
    }

}

@media screen and (min-width: 1400px){
    .describe-block{
        background-color: #fff;
        padding: 90px 100px;
        justify-content: space-evenly;
        margin-bottom: 149px;
        // max-width: 1282px;
        max-width: 1300px;
    }
    .describe-item {
        width: 42%;
    }
    .slick-wrap-w{
        width: 1390px;
        .slider-w{
            width: 1220px;
        }
    }
    .slick-wrap-w .slider-w .slider-item{
        width: 280px;
        height: 400px;
        margin: 0px 30px;
    }
    .slick-wrap-w .slider-w .slider-header{
        font-size: 2.4rem;;
    }
    .slick-wrap-w .slider-w .slider-bottom-text{
        font-size: 1.6rem ;
    }
}

@media screen and (min-width: 1700px){
    .slick-wrap-w{
        width: 1620px;
        .slider-w{
            width: 1320px;
        }
    }

}